.messages {
  display: flex;
  justify-content: center;

  .container {
    width: 1400px;
    padding: 50px 0px;

    .title {
      display: flex;
      justify-content: space-between;

      button {
        background-color: #1dbf73;
        color: white;
        font-weight: 500;
        border: none;
        padding: 10px;
        cursor: pointer;
      }
    }

    table {
      width: 100%;

      tr {
        height: 100px;
        th {
          text-align: left;
        }
        td {
          padding: 10px;

          &:first-child {
            font-weight: 500;
          }
          
          &:nth-child(2),
          &:nth-child(3) {
            color: gray;
          }
        }

        button {
          background-color: #1dbf73;
          color: white;
          padding: 10px;
          border: none;
          cursor: pointer;
        }

        &.active {
          background-color: #1dbf730f;
        }
      }
    }
  }
}

/* Media Queries */
@media only screen and (max-width: 600px) {
  .messages .container {
    width: 100%;
    padding: 20px;
  }

  .messages .container .title {
    flex-direction: column;
    align-items: center;

    button {
      margin-top: 10px;
    }
  }

  .messages .container table tr {
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .messages .container table td {
    text-align: center;
    padding: 5px;

    &:first-child {
      font-weight: normal;
    }
  }
}
