.message {
  display: flex;
  justify-content: center;

  .container {
    width: 100%;
    max-width: 1200px;
    margin: 50px;

    .breadcrumbs {
      font-weight: 300;
      font-size: 13px;
      color: #555;
    }

    .messages {
      margin: 30px 0px;
      padding: 50px;
      display: flex;
      flex-direction: column;
      gap: 20px;
      max-height: 500px;
      overflow: scroll;

      .item {
        display: flex;
        gap: 20px;
        max-width: 600px;
        font-size: 18px;

        &.owner {
          flex-direction: row-reverse;
          align-self: flex-end;

          p {
            max-width: 500px;
            border-radius: 20px 0px 20px 20px;
            background-color: royalblue;
            color: white;
          }
        }

        img {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          object-fit: cover;
        }

        p {
          max-width: 500px;
          padding: 20px;
          background-color: rgb(244, 241, 241);
          border-radius: 0px 20px 20px 20px;
          color: gray;
          font-weight: 300;
        }
      }
    }

    hr {
      height: 0.5px;
      border: none;
      background-color: rgb(232, 230, 230);
      margin-bottom: 20px;
    }

    .write {
      display: flex;
      align-items: center;
      justify-content: space-between;

      textarea {
        width: 80%;
        height: 100px;
        padding: 10px;
        border: 1px solid lightgray;
        border-radius: 10px;
      }

      button {
        background-color: #1dbf73;
        padding: 20px;
        color: white;
        font-weight: 500;
        border: none;
        border-radius: 10px;
        cursor: pointer;
        width: 100px;
      }
    }
  }
}

@media (max-width: 768px) {
  .message .container {
    margin: 20px;
    padding: 30px;

    .messages {
      padding: 20px;
      height: 300px;

      .item {
        font-size: 14px;

        img {
          width: 30px;
          height: 30px;
        }

        p {
          padding: 10px;
        }
      }
    }

 .write {
  display: flex;
  flex-direction: column;  
  textarea {
    height: 80px;
  }

  button {
    display: block;
    margin-top:1rem;
  }
}
  }
}
