.gigs {
  width: 100%;
  display: flex;
  justify-content: center;

  .container {
    width: 1400px;
    padding: 30px 0px;
    display: flex;
    flex-direction: column;
    gap: 15px;

    .breadcrumbs {
      font-weight: 300;
      text-transform: uppercase;
      font-size: 13px;
      color: #555;
    }

    h1 {
    }

    p {
      color: #999;
      font-weight: 300;
    }

    .menu {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;

      .left {
        display: flex;
        align-items: center;
        gap: 10px;
        color: #555;
        font-weight: 300;

        input {
          padding: 5px;
          border: 1px solid lightgrey;
          border-radius: 5px;
          outline: none;

          &::placeholder {
            color: #999;
          }
        }

        button {
          padding: 5px 10px;
          background-color: #1dbf73;
          color: white;
          border: none;
          font-weight: 500;
          border-radius: 5px;
          cursor: pointer;
        }
      }

      .right {
        position: relative;
        display: flex;
        align-items: center;
        gap: 10px;

        .sortBy {
          color: #555;
          font-weight: 300;
        }

        .sortType {
          font-weight: 500;
        }

        img {
          width: 15px;
          cursor: pointer;
        }

        .rightMenu {
          padding: 20px;
          background-color: white;
          border: 0.5px solid lightgrey;
          border-radius: 5px;
          position: absolute;
          top: 30px;
          right: 0;
          z-index: 9;
          display: flex;
          flex-direction: column;
          gap: 20px;
          color: #555;

          span {
            cursor: pointer;
          }
        }
      }
    }

    .cards {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
}

@media screen and (max-width: 767px) {
  .gigs .container {
    width: 100%;
    padding: 20px;
  }

  .gigs .breadcrumbs {
    font-size: 12px;
  }

  .gigs h1 {
    font-size: 20px;
  }

  .gigs p {
    font-size: 14px;
  }

  .gigs .menu {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;

    .left {
      flex-direction: column;
      align-items: flex-start;
      gap: 5px;

      input {
        width: 100%;
      }
    }

    .right {
      flex-direction: column;
      align-items: flex-start;

      .sortType {
        margin-top: 10px;
      }

      img {
        width: 12px;
      }

      .rightMenu {
        top: 25px;
        right: -10px;
        padding: 10px;
        gap: 10px;
        font-size: 14px;

        span {
          font-size: 14px;
        }
      }
    }
  }

  .gigs .cards {
    flex-direction: column;
    gap: 15px;
  }
}
