.myGigs {
  display: flex;
  justify-content: center;
  color: #555;

  .container {
    width: 100%;
    max-width: 1400px;
    padding: 50px;

    .title {
      display: flex;
      justify-content: space-between;

      button {
        background-color: #1dbf73;
        color: white;
        font-weight: 500;
        border: none;
        padding: 10px;
        cursor: pointer;
      }
    }

    table {
      width: 100%;

      tr {
        height: 50px;
        th {
          text-align: left;
        }
        td{
          color: red;
          sup {
            font-size: 12px;
          }

          .image {
            width: 50px;
            height: 25px;
            object-fit: cover;
          }

          .delete {
            width: 20px;
            cursor: pointer;
          }
        }

        &:nth-child(even) {
          background-color: #1dbf730f;
        }
      }
    }
  }
}

td.warm {
  background-color: red;
  text-align: center;
}

td:nth-last-of-type(1){
  text-align: center;
  font-weight: bold;
  font-size:20px
}